<template>
  <el-dialog title="部屋新規" :visible="dialogStatus">
    <el-form :model="data">
      <el-form-item label="部屋名" :label-width="formLabelWidth">
        <el-input v-model="data.name" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="部屋数" :label-width="formLabelWidth">
        <el-input v-model="data.stock_max" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="OB最大数" :label-width="formLabelWidth">
        <el-input v-model="data.overbooking_thresh" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="料金" :label-width="formLabelWidth">
        <el-input v-model="data.price" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="状態" :label-width="formLabelWidth">
        <el-input v-model="data.status" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="toggleModal">キャンセル</el-button>
      <el-button type="primary" @click="Create">新規</el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    props: {
      dialogStatus: Boolean,
      toggleModal: Function
    },
    data() {
      return {
        data: {
          name: '',
          stock_max: 0,
          overbooking_thresh: 0,
          price: 0,
          status: 0,
          facility_id: Number
        },
        formLabelWidth: '120px'
      }
    },
    methods: {
      Create () {
        this.axios.post('/api/v1/rooms', {
          room: this.data
        })
        .then(res => {
          console.log('success', res)
        })
        .catch(error => {
          console.log('error', error)
        })
      }
    }
  }
</script>